@import url("https://fonts.googleapis.com/css?family=Racing+Sans+One|Vast+Shadow");

.checkbox{
  height: 16px !important;
  width: 16px !important;
  display: inline-block;
  border: solid black 1px !important;
}

.checkbox-group{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  border-bottom: solid 2px rgba(175,175,175,0.6);
  background-color: rgba(0,0,0,0.3);
  border-radius: 8px;
  margin-bottom: 10px;
}

.checkbox-group > input {
  margin: auto 10px;

}

.iconDisp{
  height: 18px;
  width: 18px;
  margin-left: 10px;
  display: -ms-flexbox;
  display: flex;
  
}

.labelWrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1
  
}

.resLabel{
  font-size: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 0px;
  margin-left: 5px;
  /* font-weight: bold; */
  /* font-family: 'Racing Sans One', cursive; */
  /* font-family: 'Vast Shadow', cursive; */
  
}

.textShadow{
  text-shadow: 
  -1px -1px 0 #222222,
  1px -1px 0 #222222,
  -1px 1px 0 #222222,
  1px 1px 0 #222222;
}




